<template>
  <div id="admin" v-if="isShow">
    <aside :class="{showSidebar:!collapsed}">
      <!--展开折叠开关-->
      <div class="menu-toggle" @click.prevent="collapse">
        <i class="el-icon-arrow-down" v-show="!collapsed"></i>
        <i class="el-icon-more" v-show="collapsed"></i>
      </div>
      <!-- 导航菜单 -->
      <!--导航菜单-->
      <el-menu :default-active="defaultActiveIndex" :collapse="collapsed" @select="handleSelect">
        <template v-for="(item,index) in items" v-if="item.menuShow">
          <el-submenu v-if="!item.leaf" :index="index+''" :key="index">
            <template slot="title"><i :class="item.icon" class="fa fa-2x" style="vertical-align:middle;width:28px; text-align:center; margin-right:20px;"></i><span slot="title">{{item.name}}</span></template>
            <el-menu-item v-for="term in item.children" :key="term.name" :index="term.name" v-if="term.menuShow">
              <i :class="term.iconCls"></i><span slot="title">{{term.name}}</span>
            </el-menu-item>
          </el-submenu>
          <el-menu-item v-else-if="item.leaf&&item.children&&item.children.length" :index="item.children[0].name" :key="item.children[0].name">
            <i :class="item.icon" class="fa fa-2x" style="vertical-align:middle;width:28px; text-align:center; margin-right:20px;"></i><span slot="title">{{item.children[0].name}}</span>
          </el-menu-item>
        </template>
      </el-menu>
    </aside>
    <div class="content-container">
      <keep-alive>
        <user-list :is="this.currentPath" :current-tab="currentTab" :isRequested="isRequested"></user-list>
      </keep-alive>
    </div>
  </div>
</template>

<script>
import UserList from "@/components/admin/user-list";
import ArtEdit from "@/components/admin/artEdit";
import ObjEdit from "@/components/admin/objEdit";
import PrdEdit from "@/components/admin/prdEdit";
import MetEdit from "@/components/admin/metEdit";

export default {
  name: "admin",
  components: { UserList, ArtEdit, ObjEdit, PrdEdit, MetEdit },
  props: ["isShow"],
  data() {
    return {
      isRequested: false,
      //默认高亮标签
      defaultActiveIndex: "用户列表",
      //折叠展开的标志
      collapsed: false,
      //当前path；储存右侧内容组件名
      currentPath: "user-list",
      //当前选中标签；
      currentTab: "",
      items: [
        {
          name: "用户",
          path: "",
          leaf: true,
          icon: "fa-user",
          menuShow: true,
          children: [{ path: "user-list", name: "用户列表", menuShow: true }]
        },
        // {
        //   name: "首页",
        //   path: "/home",
        //   leaf: false,
        //   icon: "fa-home",
        //   menuShow: true,
        //   children: [
        //     { path: "", name: "近期活动", menuShow: true },
        //     { path: "", name: "协会活动", menuShow: true },
        //     { path: "", name: "新闻热点", menuShow: true },
        //     { path: "", name: "其他", menuShow: true }
        //   ]
        // },
        {
          name: "资讯",
          path: "/news",
          leaf: false,
          menuShow: true,
          icon: "fa-commenting",
          children: [
            { path: "artEdit", name: "行业动态", menuShow: true },
            { path: "artEdit", name: "资讯热点", menuShow: true },
            { path: "artEdit", name: "行业报告", menuShow: true }
          ]
        },
        {
          name: "政策",
          path: "/policy",
          leaf: true,
          menuShow: true,
          icon: "fa-file-text",
          children: [{ path: "artEdit", name: "政策", menuShow: true }]
        },
        {
          name: "协会",
          path: "/association",
          leaf: false,
          menuShow: true,
          icon: "fa-users",
          children: [
            { path: "artEdit", name: "通知公告", menuShow: true },
            { path: "artEdit", name: "协会动态", menuShow: true },
            { path: "objEdit", name: "协会会员", menuShow: true },
            // { path: "", name: "协会之窗", menuShow: true },
            { path: "artEdit", name: "教育培训", menuShow: true }
          ]
        },
        {
          name: "企业",
          path: "/enterprise",
          leaf: false,
          menuShow: true,
          icon: "fa-globe",
          children: [
            { path: "artEdit", name: "企业新闻", menuShow: true },
            // { path: "", name: "品牌展厅", menuShow: true },
            { path: "artEdit", name: "会员动态", menuShow: true }
          ]
        },
        {
          name: "产品",
          path: "/product",
          leaf: false,
          menuShow: true,
          icon: "fa-android",
          children: [
            { path: "artEdit", name: "产品推荐", menuShow: true },
            { path: "artEdit", name: "产品新闻", menuShow: true },
            { path: "artEdit", name: "产品评测", menuShow: true },
            { path: "prdEdit", name: "产品库", menuShow: true }
          ]
        },
        {
          name: "技术",
          path: "/tech",
          leaf: false,
          menuShow: true,
          icon: "fa-lightbulb-o",
          children: [
            // { path: "", name: "技术研讨", menuShow: true },
            { path: "artEdit", name: "基础知识", menuShow: true },
            { path: "artEdit", name: "案例库", menuShow: true }
          ]
        },
        {
          name: "供需",
          path: "/io",
          leaf: false,
          menuShow: false,
          icon: "fa-tachometer"
        },
        {
          name: "商城",
          path: "/mall",
          leaf: false,
          menuShow: false,
          icon: "fa-shopping-cart"
        },
        {
          name: "人才",
          path: "/talent",
          leaf: false,
          menuShow: true,
          icon: "fa-id-badge",
          children: [
            { path: "objEdit", name: "专家库", menuShow: true },
            { path: "artEdit", name: "专家智点", menuShow: true },
            { path: "objEdit", name: "高校", menuShow: true },
            // { path: "", name: "最新招聘", menuShow: true }
          ]
        },
        {
          name: "展会",
          path: "/exhibition",
          leaf: false,
          menuShow: true,
          icon: "fa-tasks",
          children: [
            { path: "metEdit", name: "展会动态", menuShow: true },
            { path: "metEdit", name: "未来展会", menuShow: true },
            { path: "metEdit", name: "展会回顾", menuShow: true }
          ]
        },
        {
          name: "会议",
          path: "/meeting",
          leaf: false,
          menuShow: true,
          icon: "fa-pencil-square-o",
          children: [
            { path: "metEdit", name: "会议动态", menuShow: true },
            { path: "metEdit", name: "未来会议", menuShow: true },
            { path: "metEdit", name: "会议回顾", menuShow: true }
          ]
        },
        {
          name: "公众号",
          path: "/public",
          leaf: true,
          menuShow: false,
          icon: "fa-weixin "
        },
        {
          name: "关于我们",
          path: "/about",
          leaf: true,
          menuShow: false,
          icon: "fa-address-book"
        },
        {
          name: "广告",
          path: "",
          leaf: false,
          menuShow: false,
          icon: "fa-bullhorn",
          children: [
            { path: "", name: "广告1", menuShow: true },
            { path: "", name: "广告2", menuShow: true },
            { path: "", name: "广告3", menuShow: true }
          ]
        }
      ],
      timeId: null
    };
  },
  methods: {
    //更改左侧边栏目折叠展开的标志
    collapse: function() {
      this.collapsed = !this.collapsed;
    },
    //点击左侧标题后更改data中的currentTab以切换右侧组件
    handleSelect(title) {
      let that = this;
      clearTimeout(that.timeId);
      this.timeId = setTimeout(function() {
        let items = that.items;
        let i, j;
        for (i in items) {
          let item = items[i];
          if (item.menuShow != false) {
            for (j in item.children) {
              let term = item.children[j];
              if (term.name === title) {
                that.currentPath = term.path;
                // console.log(that.currentPath);
                that.isRequested = that.isRequested == true ? false : true; //同组件不同tab切换监听
              }
            }
          }
        }
        that.currentTab = title;
      }, 300);
    }
  }
};
</script>
<style lang="scss" scoped>
#admin:after {
  display: block;
  content: ".";
  clear: both;
  line-height: 0;
  visibility: hidden;
}
#admin {
  display: flex;
  bottom: 0;
  overflow: hidden;
  margin: 0 auto;
  padding: 40px 20px;
  width: 84.5%;
  height: 100%;
  zoom: 1;
  // width: 100%;
  .main {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    position: absolute;
    top: 50px;
    bottom: 0px;
    overflow: hidden;
  }
  aside {
    min-width: 65px;
    padding-bottom: 50px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    background: #333744;
    &::-webkit-scrollbar {
      display: none;
    }
    &.showSidebar {
      overflow-x: hidden;
      overflow-y: auto;
    }
    .el-menu {
      height: 100%; /*写给不支持calc()的浏览器*/
      height: -moz-calc(100% - 80px);
      height: -webkit-calc(100% - 80px);
      height: calc(100% - 80px);
      border-radius: 0px;
      background-color: #333744;
      border-right: 0px;
    }
    .el-submenu {
      color: #fff;
    }
    .el-submenu .el-menu-item {
      min-width: 60px;
    }
    .el-menu {
      width: 160px;
    }
    .el-menu--collapse {
      width: 60px;
    }
    .el-submenu__title {
      color: white;
    }
    .el-submenu__title * {
      height: 46px;
      line-height: 46px;
      color: #fff;
    }
    .el-menu .el-menu-item,
    .el-submenu .el-submenu__title {
      height: 46px;
      line-height: 46px;
      color: #fff;
      background-color: #333744;
    }
    .el-menu-item i {
      color: #fff;
    }
    .el-menu-item:hover,
    .el-submenu .el-menu-item:hover {
      background-color: #7ed2df;
      color: #333744;
    }
    .el-submenu:hover {
      background-color: #7ed2df;
    }
    .el-submenu:hover .el-submenu__title * {
      color: #333744;
    }
  }
  .menu-toggle {
    background: #4a5064;
    text-align: center;
    color: white;
    height: 26px;
    line-height: 30px;
  }
  .content-container {
    background: rgba(103, 117, 136, 0.89);
    flex: 1;
    overflow-y: auto;
    padding: 10px;
    padding-bottom: 15px;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
  }
}
.el-menu-item:hover,
.el-submenu .el-menu-item:hover,
.el-submenu__title:hover {
  background-color: #7ed2df;
}
.is-active {
  color: rgb(64, 179, 255) !important;
}
</style>



