<template>
  <div id="prodEdit">
    <el-row class="warp">
      <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="拼命加载中">
        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
          <el-form :inline="true" :model="filters">
            <el-form-item>
              <el-input v-model="filters.content" placeholder="名称" @keyup.enter.native="handleSearch"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="handleSearch">查询</el-button>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="showAddDialog">新增</el-button>
            </el-form-item>
          </el-form>
        </el-col>

        <!--列表-->
        <el-table :data="items" highlight-current-row @selection-change="selsChange" style="width: 100%; margin-bottom:10px;">
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column type="index" width="60"></el-table-column>
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form label-position="left" inline class="demo-table-expand">
                <el-form-item label="[图文简介]">
                  <img :src="props.row.img" alt="">
                  <div v-html="props.row.introduction" style="display:block;"></div>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column prop="name" label="名称" sortable></el-table-column>
          <el-table-column prop="price" label="价格" width="100" sortable :formatter="formatPrice"></el-table-column>
          <el-table-column prop="lastUpdateTime" label="更新日期" width="150" sortable></el-table-column>
          <el-table-column label="操作" width="200">
            <template slot-scope="scope">
              <el-button size="small" @click="showEditDialog(scope.$index,scope.row)">编辑</el-button>
              <el-button type="danger" @click="delItem(scope.$index,scope.row)" size="small">删除</el-button>
            </template>
          </el-table-column>
        </el-table>

        <!--工具条-->
        <el-col :span="24" class="toolbar">
          <el-button type="danger" @click="batchDeleteItems" :disabled="this.sels.length===0">批量删除</el-button>
          <el-pagination layout="prev, pager, next" background @current-change="handleCurrentChange" :page-size="pageSize" :total="total" style="float:right;">
          </el-pagination>
        </el-col>

        <!-- 编辑界面 -->
        <el-dialog title="编辑" :visible.sync="editFormVisible" :close-on-click-modal="true" top="8vh" :modal-append-to-body="false">
          <el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
            <el-form-item label="名称" prop="name">
              <el-input v-model="editForm.name" auto-complete="off"></el-input>
            </el-form-item>
            <!-- <el-form-item label="价格" prop="price">
              <el-input v-model="editForm.price" auto-complete="off"></el-input>
            </el-form-item> -->
            <el-form-item label="更新日期" prop="lastUpdateTime">
              <el-date-picker type="date" placeholder="选择更新日期" v-model="editForm.lastUpdateTime"></el-date-picker>
            </el-form-item>
            <el-form-item label="封面" prop="img">
              <el-upload class="avatar-uploader" action="http://122.13.4.239/common/uploadImg" :http-request="editImgUpload" :auto-upload="true" :show-file-list="false" :before-upload="beforeAvatarUpload">
                <img v-if="editCoverShow" :src="editForm.img" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <el-form-item label="简介内容" prop="introduction">
              <div @click="clickImg($event)">
                <vue-editor class="editor1" :editorToolbar="customToolbar" v-model="editForm.introduction" useCustomImageHandler @imageAdded="handleImageAdded"></vue-editor>
              </div>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click.native="editFormVisible = false">取消</el-button>
            <el-button type="primary" @click.native="editSubmit">提交</el-button>
          </div>
        </el-dialog>

        <!--新增界面-->
        <el-dialog title="新增" :visible.sync="addFormVisible" :close-on-click-modal="true" top="8vh" :modal-append-to-body="false">
          <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
            <el-form-item label="名称" prop="name">
              <el-input v-model="addForm.name" auto-complete="off"></el-input>
            </el-form-item>
            <!-- <el-form-item label="价格" prop="price">
              <el-input v-model="addForm.price" auto-complete="off"></el-input>
            </el-form-item> -->
            <el-form-item label="更新日期" prop="lastUpdateTime">
              <el-date-picker type="date" placeholder="选择更新日期" v-model="addForm.lastUpdateTime"></el-date-picker>
            </el-form-item>
            <el-form-item label="封面" prop="img">
              <el-upload class="avatar-uploader" action="http://122.13.4.239/common/uploadImg" :http-request="addImgUpload" :auto-upload="true" :show-file-list="false" :before-upload="beforeAvatarUpload">
                <img v-if="addCoverShow" :src="editForm.img" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <el-form-item label="简介内容" prop="introduction">
              <div @click="clickImg($event)">
                <vue-editor class="editor1" :editorToolbar="customToolbar" v-model="addForm.introduction" useCustomImageHandler @imageAdded="handleImageAdded"></vue-editor>
              </div>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click.native="addFormVisible = false">取消</el-button>
            <el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
          </div>
        </el-dialog>
      </el-col>
    </el-row>
    <el-dialog title="更改图片比例" :visible.sync="imgFormVisible" width="30%" top="8vh" :close-on-click-modal="true" :modal-append-to-body="false">
      <el-form :model="imgForm">
        <el-form-item label="高(px)">
          <el-input v-model="imgForm.height" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="宽(px)">
          <el-input v-model="imgForm.width" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="imgFormVisible = false">取 消</el-button>
        <el-button type="primary" @click.native="imgEdited" :loading="imgLoading">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import API from "@/api/api_admin";
import Qs from "qs";
import { VueEditor } from "vue2-editor";

export default {
  name: "prdEdit",
  components: { VueEditor },
  props: ["currentTab", "isRequested"],
  data() {
    return {
      categoryId: 0,
      //搜索框内容
      filters: {
        content: ""
      },
      //组件展示内容
      items: [
        {
          id: "",
          name: "广东工业大学",
          lastUpdateTime: "2018-08-20",
          price: "广东省经济和信息化委员会",
          introduction: "asdasdasd"
        }
      ],
      total: 1, //文章总数
      pageSize: 12, //每页文章数
      page: 1, //当前所在页
      // limit: 10,
      loading: false, //loading  标志
      sels: [], //列表选中列
      //页面初始化相关数据
      //用于判断当前该发请求的categoryId
      initFormRules: [],
      customToolbar: [
        // [{ 'size': ['small', false, 'large', 'huge'] }],
        [{ header: [false, 1, 2, 3, 4, 5, 6] }],
        ["bold", "italic", "underline", "strike"],
        ["clean"],
        [
          { align: "" },
          { align: "center" },
          { align: "right" },
          { align: "justify" }
        ],
        [{ list: "ordered" }, { list: "bullet" }],
        [{ indent: "-1" }, { indent: "+1" }],
        ["blockquote", "code-block"],
        ["image", "link"]
      ],
      //编辑相关数据
      editCoverShow: false, //判断编辑封面显示
      editFormVisible: false, //编辑界面是否显示
      // 表单验证规则
      editFormRules: {
        name: [{ required: true, message: "请输入名称", trigger: "blur" }],
        // price: [{ required: false, message: "请输入价格", trigger: "blur" }],
        introduction: [
          { required: false, message: "请输入内容", trigger: "blur" }
        ],
        img: [{ required: false, message: "请输入图片地址", trigger: "blur" }]
        // lastUpdateTime: [
        //   { required: true, message: "请填写更新日期", trigger: "blur" }
        // ]
      },
      //ref操作dom
      editForm: {
        id: 0,
        name: "",
        // price: "",
        // lastUpdateTime: "",
        introduction: "asdasdasd",
        img: ""
      },
      //新增相关数据
      addFormVisible: false, //新增界面是否显示
      addCoverShow: false,
      addLoading: false,
      // 表单验证规则
      addFormRules: {
        name: [{ required: true, message: "请输入名称", trigger: "blur" }],
        // price: [{ required: false, message: "请输入价格", trigger: "blur" }],
        introduction: [
          { required: false, message: "请输入内容", trigger: "blur" }
        ],
        img: [{ required: false, message: "请输入图片地址", trigger: "blur" }]
        // lastUpdateTime: [
        //   { required: true, message: "请填写更新日期", trigger: "blur" }
        // ]
      },
      //ref操作dom
      addForm: {
        name: "",
        // price: "",
        // lastUpdateTime: "",
        introduction: "asdasdasd",
        img: ""
      },
      //当前修改的图片
      currentImg: new Object(),
      imgFormVisible: false, //图片更改比例界面是否显示
      //图片修改数据
      imgForm: {
        height: "",
        width: ""
      },
      imgLoading: false
    };
  },
  methods: {
    formatPrice: function(row, column) {
      return row.price == 0 ? "￥" : row.price;
    },
    //点击富文本编辑器绑定图片点击事件
    clickImg(e) {
      var el = e.target;
      if (el.tagName == "IMG") {
        this.currentImg = e.target;
        this.imgForm.height = el.height;
        this.imgForm.width = el.width;
        el.title = "点击编辑";
        this.imgFormVisible = true;
      }
    },
    //图片比例更改上传
    imgEdited() {
      var l = parseFloat(this.imgForm.height);
      var w = parseFloat(this.imgForm.width);
      this.currentImg.width = w;
      this.currentImg.height = l;
      this.imgFormVisible = false;
    },
    //换页
    handleCurrentChange(val) {
      this.page = val;
      this.search();
    },
    //处理富文本框图片上传
    handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      let that = this;
      let type = file.name.split(".");
      type = type.pop(); //获取文件类型
      // let params = Qs.stringify({
      //     imgs: file,
      //   });
      let formData = new FormData();
      let url;
      if (
        type.indexOf("png") > -1 ||
        type.indexOf("jpg") > -1 ||
        type.indexOf("jpeg") > -1 ||
        type.indexOf("gif") > -1 ||
        type.indexOf("pdf") > -1
      ) {
        formData.append("imgs", file);
        url = that.HOST + "/common/uploadImg";
      } else {
        formData.append("videos", file);
        url = that.HOST + "/common/uploadVideo";
      }
      that
        .axios({
          url: url,
          method: "POST",
          headers: { "Content-Type": "multipart/form-data" },
          data: formData
        })
        .then(response => {
          if (response.data.result == "success") {
            let url = response.data.message; // Get url from response
            if (
              type.indexOf("png") > -1 ||
              type.indexOf("jpg") > -1 ||
              type.indexOf("jpeg") > -1 ||
              type.indexOf("gif") > -1 ||
              type.indexOf("pdf") > -1
            ) {
              Editor.insertEmbed(cursorLocation, "image", url[0]); //图片
            } else {
              Editor.insertEmbed(cursorLocation, "video", url[0]); //视频
            }
            // resetUploader();
          } else {
            that.$message.error({
              showClose: true,
              message: "上传异常",
              duration: 2000
            });
          }
        })
        .catch(err => {
          console.log(err);
          that.$message.error({
            showClose: true,
            message: "上传异常",
            duration: 2000
          });
        });
    },
    //封面图片格式要求
    beforeAvatarUpload(file) {
      // const isJPG = file.type === 'image/jpeg';
      const isLt10M = file.size / 1024 / 1024 < 10;
      // if (!isJPG) {
      //   this.$message.error('上传头像图片只能是 JPG 格式!');
      // }
      if (!isLt10M) {
        this.$message.error("上传头像图片大小不能超过 10MB!");
      }
      // return isJPG && isLt10M;
    },
    //新增
    //新增弹框
    showAddDialog() {
      this.addFormVisible = true;
      this.addForm = {
        name: "",
        introduction: ""
      };
    },
    //新增框上传封面图片操作
    addImgUpload(e) {
      let that = this;
      // let params = Qs.stringify({
      //     imgs: file,
      //   });
      let url = that.HOST + "/common/uploadImg";
      let formData = new FormData();
      formData.append("imgs", e.file);
      that
        .axios({
          url: url,
          method: "POST",
          headers: { "Content-Type": "multipart/form-data" },
          data: formData
        })
        .then(response => {
          if (response.data.result == "success") {
            that.$message.success({
              showClose: true,
              message: "上传成功",
              duration: 2000
            });
            that.addCoverShow = true;
            that.addForm.img = response.data.message[0];
          } else {
            that.$message.error({
              showClose: true,
              message: "图片上传异常",
              duration: 2000
            });
          }
        })
        .catch(err => {
          console.log(err);
          that.$message.error({
            showClose: true,
            message: "图片上传异常",
            duration: 2000
          });
        });
    },
    //新增插入
    addSubmit() {
      let that = this;
      let para = Object.assign({}, this.addForm);
      if (para.name) {
        that.$confirm("确认添加吗", "新增", { type: "warning" }).then(() => {
          that.addForm.introduction = that.addForm.introduction
            .replace(/<\/div>/g, "</p>")
            .replace(/<div/g, "<p"); //此处vue2-editor的v-model无法显示div
          let url = that.HOST + "/product/manager/addProduct";
          let params = Qs.stringify({
            name: para.name,
            // price: para.price,
            lastUpdateTime: para.lastUpdateTime,
            introduction: para.introduction,
            img: para.img
          });
          that.loading = true;
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(function(response) {
              that.loading = false;
              if (response.data.result == "success") {
                that.$message.success({
                  showClose: true,
                  message: "新增成功",
                  duration: 1500
                });
                that.addFormVisible = false;
                that.search();
              } else {
                let err = response.data.message;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
                that.addFormVisible = false;
              }
            })
            .catch(response => {
              that.loading = false;
              that.$message.error({
                showClose: true,
                message: "请求出现异常",
                duration: 2000
              });
            });
        });
      } else {
        that.$message.warning({
          showClose: true,
          message: "必填数据不能为空",
          duration: 1000
        });
      }
    },
    //删除
    delItem(index, row) {
      let that = this;
      this.$confirm("确认删除该记录吗?", "提示", { type: "warning" })
        .then(() => {
          that.loading = true;
          let postId = row.id;

          let url = that.HOST + "/product/manager/deleteProduct";
          let params = Qs.stringify({
            ids: postId
          });
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(response => {
              if (response.data.result == "success") {
                that.loading = false;
                that.$message.success({
                  showClose: true,
                  message: "删除成功",
                  duration: 1500
                });
                that.search();
              } else {
                let err = response.data.message;
                that.loading = false;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
              }
            })
            .catch(function(error) {
              that.loading = false;
              console.log(error);
              that.$message.error({
                showClose: true,
                message: "请求出现异常",
                duration: 2000
              });
            });
        })
        .catch(() => {});
    },
    //批量选中
    selsChange: function(sels) {
      this.sels = sels;
    },
    //批量删除
    batchDeleteItems() {
      let that = this;
      this.$confirm("确认删除选中记录吗？", "提示", {
        type: "warning"
      })
        .then(() => {
          that.loading = true;
          let ids = that.sels.map(item => item.id).toString();
          let url = that.HOST + "/product/manager/deleteProduct";
          let params = Qs.stringify({
            ids: ids
          });
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(response => {
              if (response.data.result == "success") {
                that.loading = false;
                that.$message.success({
                  showClose: true,
                  message: "删除成功",
                  duration: 1500
                });
                that.search();
              } else {
                let err = response.data.message;
                that.loading = false;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
              }
            })
            .catch(function(error) {
              that.loading = false;
              console.log(error);
              that.$message.error({
                showClose: true,
                message: "请求出现异常",
                duration: 2000
              });
            });
        })
        .catch(() => {});
    },
    //搜索
    handleSearch() {
      this.total = 0;
      this.page = 1;
      this.search();
    },
    //显示搜索（或初始化）
    search() {
      //   console.log(this.currentTab);
      let that = this;

      let url = that.HOST + "/common/search";
      let content = that.filters.content.trim();

      // if (that.categoryId != "") {
      let data = {
        pageNum: that.page, //页数，默认为1
        content: content,
        channel: 2
      };
      //启动loading动画
      that.loading = true;
      //发送get请求
      that.axios
        .get(url, {
          params: data
        })
        .then(response => {
          that.items = response.data.message.results.list;
          that.items.forEach((item, index) => {
            if (item.lastUpdateTime)
              that.items[index].lastUpdateTime = item.lastUpdateTime.slice(
                0,
                10
              );
          });
          that.total = response.data.message.results.total;
          that.pageSize = response.data.message.results.pageSize;
          that.loading = false;
        })
        .catch(response => {
          that.loading = false;
          that.items = [];
          that.total = 0;
          that.$message.error({
            showClose: true,
            message: "请求出现异常",
            duration: 2000
          });
        });
    },

    //编辑
    //编辑弹框
    showEditDialog(index, row) {
      this.editFormVisible = true;
      if (row.img) {
        this.editCoverShow = true;
      }
      this.editForm = Object.assign({}, row);
      this.editForm.introduction = this.editForm.introduction
        .replace(/<\/div>/g, "</p>")
        .replace(/<div/g, "<p"); //此处vue2-editor的v-model无法显示div
    },
    //编辑封面图片
    editImgUpload(e) {
      let that = this;
      // let params = Qs.stringify({
      //     imgs: file,
      //   });
      let url = that.HOST + "/common/uploadImg";
      let formData = new FormData();
      formData.append("imgs", e.file);
      that
        .axios({
          url: url,
          method: "POST",
          headers: { "Content-Type": "multipart/form-data" },
          data: formData
        })
        .then(response => {
          if (response.data.result == "success") {
            that.editCoverShow = true;
            that.editForm.img = response.data.message[0];
          } else {
            that.$message.error({
              showClose: true,
              message: "图片上传异常",
              duration: 2000
            });
          }
        })
        .catch(err => {
          console.log(err);
          that.$message.error({
            showClose: true,
            message: "图片上传异常",
            duration: 2000
          });
        });
    },
    //编辑插入
    editSubmit() {
      let that = this;
      let para = Object.assign({}, this.editForm);
      if (para.name) {
        that.$confirm("确认更改吗", "编辑", { type: "warning" }).then(() => {
          let url = that.HOST + "/product/manager/updateProduct";
          let params = Qs.stringify({
            id: para.id,
            name: para.name,
            // price: para.price,
            lastUpdateTime: para.lastUpdateTime,
            introduction: para.introduction,
            img: para.img
          });
          that.loading = true;
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(function(response) {
              that.loading = false;
              if (response.data.result == "success") {
                that.$message.success({
                  showClose: true,
                  message: "编辑成功",
                  duration: 1500
                });
                that.editFormVisible = false;
                that.search();
              } else {
                let err = response.data.message;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
                that.editFormVisible = false;
              }
            })
            .catch(response => {
              that.loading = false;
              that.$message.error({
                showClose: true,
                message: "请求出现异常",
                duration: 2000
              });
            });
        });
      } else {
        that.$message.warning({
          showClose: true,
          message: "必填数据不能为空",
          duration: 1000
        });
      }
    }
  },
  activated() {
    this.handleSearch();
  },
  watch: {
    //监听组件刷新内容
    isRequested() {
      this.handleSearch();
      // console.log(this.isRequested);
    }
  }
};
</script>
