/*
 * @Description: 接收一个new Date()实例化的日期
 * @Author: your name
 * @Date: 2019-05-31 22:38:55
 * @LastEditTime: 2019-07-17 16:46:02
 * @LastEditors: Please set LastEditors
 */
function getTime(time) {
  if(!time) return '';
  var year = time.getFullYear(); //年
  var month = time.getMonth() + 1; //月
  var day = time.getDate(); //日
  var hh = time.getHours(); //时
  var mm = time.getMinutes(); //分
  var ss = time.getSeconds(); //秒
  var clock = "";
  //time：时分；date：年月日；ddate：日；m：年月
  if (hh < 10) hh = "0" + hh;
  if (mm < 10) mm = "0" + mm;
  if (ss < 10) ss = "0" + ss;
  if (month < 10) month = "0" + month;
  if (day < 10) day = "0" + day;
  clock += year + "-" + month + "-" + day + " " + hh + ":" + mm + ":" + ss;
  return clock;
}
export default getTime



