<template>
  <div id="user-list">
    <el-row class="warp">
      <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="拼命加载中">
        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
          <el-form :inline="true" :model="filters">
            <el-form-item>
              <el-input v-model="filters.name" placeholder="用户名/邮箱" style="min-width: 240px;" @keyup.enter.native="handleSearch"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="handleSearch">查询</el-button>
            </el-form-item>
          </el-form>
        </el-col>

        <!--列表-->
        <el-table :data="users" highlight-current-row v-loading="loading" style="width: 100%;">
          <el-table-column type="index" width="60">
          </el-table-column>
          <!-- <el-table-column prop="name" label="姓名" width="120" sortable>
          </el-table-column> -->
          <el-table-column prop="username" label="昵称" width="140" sortable>
          </el-table-column>
          <el-table-column prop="password" label="密码" width="140" sortable>
          </el-table-column>
          <el-table-column prop="sex" label="性别" width="100" :formatter="formatSex" sortable>
          </el-table-column>
          <el-table-column prop="email" label="邮箱" width="260" sortable>
          </el-table-column>
          <el-table-column prop="phone" label="联系方式" sortable>
          </el-table-column>
        </el-table>
        <!--工具条-->
        <el-col :span="24" class="toolbar">
          <el-pagination layout="prev, pager, next" background @current-change="handleCurrentChange" :page-size="pageSize" :total="total" style="float:right;">
          </el-pagination>
        </el-col>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "user-list",
  props: ["isRequested"],
  data() {
    return {
      filters: {
        name: ""
      },
      users: [
        {
          // name: "约翰",
          email: "123123@163.com",
          id: null,
          username: "John",
          password: "",
          sex: "1",
          phone: "",
          email: "",
          rank: ""
        }
      ],
      total: 0,
      page: 1,
      pageSize:12,
      // limit: 10,
      loading: false
    };
  },
  methods: {
    //性别显示转换
    formatSex: function(row, column) {
      return row.sex == 1 ? "男" : row.sex == 0 ? "女" : "";
    },
    //换页
    handleCurrentChange(val) {
      this.page = val;
      this.search();
    },
    //搜索
    handleSearch() {
      this.total = 0;
      this.page = 1;
      this.search();
    },
    //获取用户列表
    search: function() {
      let that = this;
      let data = {
        pageNum: that.page,
        username: that.filters.name
      };
      let url = that.HOST + "/user/manager/find";
      // 启动loading动画
      that.loading = true;
      //发送get请求
      that.axios
        .get(url, {
          params: data
        })
        .then(response => {
          that.users = response.data.message.list;
          that.total = response.data.message.total;
          that.pageSize = response.data.message.pageSize;
          that.loading = false;
        })
        .catch(response => {
          that.loading = false;
          that.users = [];
          that.total = 0;
          that.$message.error({
            showClose: true,
            message: "请求出现异常",
            duration: 2000
          });
        });
    }
  },
  activated() {
    this.search();
  },
  watch: {
    isRequested() {
      this.search();
    }
  },
  components: {}
};
</script>

<style lang="scss" scoped>
</style>



