<!--admin.vue-->
<template>
  <div id="admin">
    <div class="bg-img">
      <div class="bg-content">
        <div class="m-header1">
          <div class="media2">
            <a class="ex2" href="admin.html" target="_blank">后台管理系统</a>
          </div>
          <div class="topbar-account topbar-btn media">
            <el-dropdown trigger="click">
              <span class="el-dropdown-link userinfo-inner"><i class="fa fa-user" aria-hidden="true"></i><i class="el-icon-caret-bottom"></i></span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item>
                  <a href="index.html"><span style="color: #555;font-size: 14px;">返回前端页面</span></a>
                </el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </div>
      </div>
      <app :isShow="rank>0 && rank<= 6"></app>
    </div>
  </div>
</template>

<script>
import App from "@/components/admin/admin";
export default {
  name: "admin",
  components: {
    App
  },
  data() {
    return {
      rank: "",
      username: ""
    };
  },
  watch: {},
  methods: {
    getLoginData(prefix) {
      const url = this.HOST + "/user/getPermission";
      return this.axios({
        method: "get",
        url: url
      })
        .then(res => {
          return Promise.resolve(res.data);
        })
        .catch(err => {
          return Promise.reject(err.data);
        });
    },
    checkLoginStatus() {
      let that = this;
      //查看当前登录状态
      that
        .getLoginData()
        .then(res => {
          that.rank = res.message.role;
          if (res.message.username) {
            global.userItem.userName = res.message.username;
          }
        })
        .catch(err => {});
    }
  },
  mounted: function() {},
  created() {
    this.checkLoginStatus();
  }
};
</script>

<style lang="scss">
body {
  margin: 0;
  font-size: 16px;
  font-family: "微软雅黑", "Microsoft Yahei", Verdana, sans-serif;
  min-width: 1002px;
  // background: url(/static/PICS/ui-background.jpg) center top repeat rgb(255, 255, 255);
  // background: url(http://image.woshipm.com/wp-files/2014/09/72f9850c5a1e72f4ea873584fbb3959c.jpg) center top repeat rgb(255, 255, 255);
}
.bg-img {
  position: relative;
  // width: 100%;
  min-width: 1200px;
  min-height: 100vh;
  background: url(/static/PICS/background1.jpg) center top repeat
    rgb(255, 255, 255);
}
// .bg-img::before {
//   content: "";
//   position: absolute;
//   top: 0;
//   right: 0;
//   bottom: 0;
//   left: 0;
//   background-image: linear-gradient(to bottom right, #26486e, #cc998f);
//   opacity: 0.3;
//   z-index: 1;
// }
.bg-content {
  z-index: 2;
  position: relative;
}
ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}
input:focus {
  outline: none;
}
a {
  text-decoration: none;
  color: inherit;
}
.background-wall {
  background-color: #000000;
  width: 100%;
  height: 100%;
  top: 0;
  position: fixed;
  clear: both;
  z-index: 5;
  opacity: 0.5;
}

.m-header1 {
  & {
    min-width: 1200px;
    background-color: hsla(0, 0%, 100%, 0.1);
    color: white;
    padding: 2px 120px;
    box-sizing: border-box;
    width: 100%;
    white-space: nowrap;
    line-height: 1.3;
    zoom: 1;
  }
  &:after {
    display: block;
    content: ".";
    clear: both;
    line-height: 0;
    visibility: hidden;
  }
  .icon {
    margin: 0.24em 0;
    float: left;
    overflow: hidden;
    font-size: 16px;
  }
  .media {
    float: right;
    text-align: right;
    overflow: hidden;
    margin: 0.57em 0 0.57em 0;
    font-size: 18px;
    line-height: 1.6;
  }
  .media2 {
    @extend .media;
    float: left;
    color: #e7e7e7;
  }
  .ex1 {
    margin-right: 8px;
    cursor: pointer;
  }
  .ex2 {
    @extend .ex1;
    display: inline-block;
  }
  /*竖直分割线*/
  .vertical_line {
    padding: 0 6px;
    border-left: 1px white solid;
    vertical-align: middle;
  }
}
// 登录状态
.topbar-btn {
  color: #fff;
}
.topbar-account {
  float: right;
  padding-right: 12px;
}
.userinfo-inner {
  cursor: pointer;
  font-size: 16px;
  color: #fff;
  padding-left: 10px;
  line-height: 26px;
}
</style>



