      this.page = val;
      this.search();
    },
    //处理富文本框图片上传
    handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      console.log(file);
      let that = this;
      let type = file.name.split(".");
      type = type.pop(); //获取文件类型
      console.log(type);
      // let params = Qs.stringify({
      //     imgs: file,
      //   });
      let formData = new FormData();
      let url;
      if (
        type.indexOf("png") > -1 ||
        type.indexOf("jpg") > -1 ||
        type.indexOf("jpeg") > -1 ||
        type.indexOf("gif") > -1 ||
        type.indexOf("pdf") > -1
      ) {
        formData.append("imgs", file);
        url = that.HOST + "/common/uploadImg";
      } else {
        formData.append("videos", file);
        url = that.HOST + "/common/uploadVideo";
      }
      that
        .axios({
          url: url,
          method: "POST",
          headers: { "Content-Type": "multipart/form-data" },
          data: formData
        })
        .then(response => {
          if (response.data.result == "success") {
            let url = response.data.message; // Get url from response
            if (
              type.indexOf("png") > -1 ||
              type.indexOf("jpg") > -1 ||
              type.indexOf("jpeg") > -1 ||
              type.indexOf("gif") > -1 ||
              type.indexOf("pdf") > -1
            ) {
              Editor.insertEmbed(cursorLocation, "image", url[0]); //图片
            } else {
              Editor.insertEmbed(cursorLocation, "video", url[0]); //视频
            }
            // resetUploader();
          } else {
            that.$message.error({
              showClose: true,
              message: "上传异常",
              duration: 2000
            });
          }
        })
        .catch(err => {
          console.log(err);
          that.$message.error({
            showClose: true,
            message: "上传异常",
            duration: 2000
          });
        });
    },
