    //新增插入
    addSubmit() {
      let that = this;
      let para = Object.assign({}, this.addForm);
      var d = new Date(para.postDate);
      var postDate = getTime(d);
      if (para.name && para.introduction) {
        that.$confirm("确认添加吗", "新增", { type: "warning" }).then(() => {
          that.addForm.introduction = that.addForm.introduction
            .replace(/<\/div>/g, "</p>")
            .replace(/<div/g, "<p"); //此处vue2-editor的v-model无法显示div
          let url = that.HOST + "/introduction/manager/addIntroduction";
          let params = Qs.stringify({
            name: para.name,
            // source: para.source,
            postDate: postDate,
            img: para.img,
            introduction: para.introduction,
            categoryId: that.categoryId
          });
          that.loading = true;
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(function(response) {
              that.loading = false;
              that.addCoverShow = true;
              if (response.data.result == "success") {
                that.$message.success({
                  showClose: true,
                  message: "新增成功",
                  duration: 1500
                });
                that.addFormVisible = false;
                that.search();
              } else {
                let err = response.data.message;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
                that.addFormVisible = false;
              }
            })
            .catch(response => {
              that.loading = false;
              that.$message.error({
                showClose: true,
                message: "请求出现异常",
                duration: 2000
              });
            });
        });
      } else {
        that.$message.warning({
          showClose: true,
          message: "必填数据不能为空",
          duration: 1000
        });
      }
    },
