        let data = {
          pageNum: that.page, //页数，默认为1
          categoryId: that.categoryId, //编号,筛选数据归属
          content: content,
          channel: 3
        };
        //启动loading动画
        that.loading = true;
        //发送get请求
        that.axios
          .get(url, {
            params: data
          })
          .then(response => {
            that.items = response.data.message.results.list;
            // that.items.forEach((item, index) => {
            //   that.items[index].postDate = item.postDate.slice(0, 10);
            // });
            that.total = response.data.message.results.total;
            that.pageSize = response.data.message.results.pageSize;
            that.loading = false;
          })
          .catch(response => {
            that.loading = false;
            that.items = [];
            that.total = 0;
            that.$message.error({
              showClose: true,
              message: "请求出现异常",
              duration: 2000
            });
          });
      } else {
        that.items = [];
        that.total = 0;
        that.$message.error({
          showClose: true,
          message: "数据异常",
          duration: 2000
        });
      }
    },

    //编辑
    //编辑弹框
    showEditDialog(index, row) {
      this.editFormVisible = true;
      if (row.img) {
        this.editCoverShow = true;
      }
      this.editForm = Object.assign({}, row);
      this.editForm.introduction = this.editForm.introduction
        .replace(/<\/div>/g, "</p>")
        .replace(/<div/g, "<p"); //此处vue2-editor的v-model无法显示div
    },
    //编辑封面图片
    editImgUpload(e) {
      let that = this;
      // let params = Qs.stringify({
      //     imgs: file,
      //   });
      let url = that.HOST + "/common/uploadImg";
      let formData = new FormData();
      formData.append("imgs", e.file);
      that
        .axios({
          url: url,
          method: "POST",
          headers: { "Content-Type": "multipart/form-data" },
          data: formData
        })
        .then(response => {
          if (response.data.result == "success") {
            that.editCoverShow = true;
            that.editForm.img = response.data.message[0];
          } else {
            that.$message.error({
              showClose: true,
              message: "图片上传异常",
              duration: 2000
            });
          }
        })
        .catch(err => {
          console.log(err);
          that.$message.error({
            showClose: true,
            message: "图片上传异常",
            duration: 2000
          });
        });
    },
    //编辑插入
    editSubmit() {
      let that = this;
      let para = Object.assign({}, this.editForm);
      var d = new Date(para.postDate);
      var postDate = getTime(d);
      if (para.name && para.introduction) {
        that.$confirm("确认更改吗", "编辑", { type: "warning" }).then(() => {
          let url = that.HOST + "/introduction/manager/updateIntroduction";
          let params = Qs.stringify({
            id: para.id,
            name: para.name,
            // source: para.source,
            postDate: postDate,
            img: para.img,
            introduction: para.introduction
            // categoryId: that.categoryId
          });
          that.loading = true;
          that
            .axios({
              method: "post",
              url: url,
              data: params
            })
            .then(function(response) {
              that.loading = false;
              if (response.data.result == "success") {
                that.$message.success({
                  showClose: true,
                  message: "编辑成功",
                  duration: 1500
                });
                that.editFormVisible = false;
                that.search();
              } else {
                let err = response.data.message;
                that.$message.error({
                  showClose: true,
                  message: err.toString(),
                  duration: 2000
                });
                that.editFormVisible = false;
              }
            })
            .catch(response => {
              that.loading = false;
              that.$message.error({
                showClose: true,
                message: "请求出现异常",
                duration: 2000
              });
            });
        });
      } else {
        that.$message.warning({
          showClose: true,
          message: "必填数据不能为空",
          duration: 1000
        });
      }
    }
  },
  activated() {
    this.handleSearch();
  },
  watch: {
    //监听组件刷新内容
    isRequested() {
      this.handleSearch();
      console.log(this.isRequested);
    }
  }
};
</script>
<style lang="scss">
.ql-snow .ql-editor code,
.ql-snow .ql-editor pre {
  background-color: #ffffff !important;
}
.el_b_form input {
  width: 50% !important;
}
.el-dialog .editor1 #quill-container {
  max-height: 1000px;
  overflow: auto;
}
.ql-snow .ql-picker {
  height: auto !important;
  margin-top: -6px;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 108px;
  height: 108px;
  line-height: 108px;
  text-align: center;
}
.avatar {
  width: 108px;
  height: 108px;
  display: block;
}
.avatar2 {
  @extend .avatar;
}
.ql-snow .ql-tooltip {
  left: 23.602px !important;
  top: 31px !important;
  color: green;
}
.ql-snow .ql-editor img {
  cursor: pointer;
}
</style>




